.nav{
  width: 100%;
  height: 10vh;
  background-color: rgb(217, 212, 212);
  display:flex;
}
.nav-title{
  width: 30%;
  text-align: center;
  color: white;
}
.nav-menu{
  width: 30%;
  text-align: left;
  display: flex;
  justify-content: space-evenly;
}
.body-container{
  width: 100%;
  height: 70vh;
  display: flex;
}
.body-left-panel{
  width: 30%;
  text-align: center;
  margin-top: 60px;
  text-align:justify;
  margin-left: 100px;
}
.body-circle{
 height: 100px;
  background-color: rgb(255, 255, 255);
  display: flex;
  text-align: center;
  justify-content: space-between;
 
}
.c-a{
  width: 50px;
  height: 50px;
  background-color: rgb(239, 255, 148);
  border-radius: 50%;
  margin-top: 50px;
}
.c-b{
  width: 60px;
  height: 60px;
  margin-top: 10px;
  background-color: rgb(237, 130, 130);
  border-radius: 50%;
}
.c-c{
  width: 80px;
  height: 80px;
  background-color: rgb(175, 194, 255);
  border-radius: 50%;
  margin-top: 30px;
}
.body-left-panel h1{
  font-family: 'Raleway', sans-serif;
  
  font-size: 50px;
  line-height: 20px;
}
.body-right-panel{
  width: 70%;
  text-align: center;
}
.footer{
  width: 100%;
  height: 20vh;
  margin-top: 30px;
  background-color: rgb(129, 129, 129);
  display: flex;
  justify-content: space-evenly;

}
.f-a ,.f-b, .f-c{
 
  text-align: justify;
  color: white;
  
}
